html {
    font-family: 'Malgun Gothic', Dotum, Verdana, AppleGothic, Sans-serif;
    min-width: 300px;
    min-height: 400px;
    height: 100%;
}

body {
    height: 100%;
}

#root {
    height: 100%;
}

*:focus {
    box-shadow: none !important;
}

button[aria-selected=true] div div{
    filter: opacity(0.5) drop-shadow(0 0 0 cyan);
}

button[aria-selected=true] {
    border-bottom: 2px solid #36CAE0;
}

.textRightIcon {
    display: inline-flex;
}

.disabledBtn {
    background-color: gray !important;
    cursor: default;
}